    console.log("to.name", to.name);
    console.log("from.name", from.name);
    if (to.name == "newLights" && from.name == "functionalMode") {
      from.meta.keepAlive = false;
      this.gotoflag = 0
    } else {
      this.gotoflag = 1
      from.meta.keepAlive = false;
      if(this.oneZoneFlag == 0){
        this.lightVal =  {
          FunctionalLightsIntensity: this.FunctionalLightsIntensity,
          FunctionalLightsColour: this.FunctionalLightsColour,
          FunctionalRGBlightIntensity: this.FunctionalRGBlightIntensity,
          FunctionalOntime: this.FunctionalOntime,
          oneZoneFlag: this.oneZoneFlag
        };
      } else {
        this.lightVal =  {
          FunctionalLightsIntensity: this.FunctionalLightsIntensity,
          FunctionalLightsColour: this.FunctionalLightsColour,
          FunctionalOntime: this.FunctionalOntime,
          oneZoneFlag: this.oneZoneFlag
        };
      }

      window.sessionStorage.setItem('lightVal', JSON.stringify(this.lightVal))

    }
    next();
