      this.$router.push({
        path: "/newLights/functionalMode",
        query: {
          from: this.from,
          language: this.$i18n.locale,
          deviceId: this.deviceId,
          fromPageName: "functionalMode",
        },
      });
    },
    goAmbientMode() {
      this.$router.push({
        path: "/newLights/ambientMode",
        query: {
          from: this.from,
          language: this.$i18n.locale,
          deviceId: this.deviceId,
          fromPageName: "ambientMode",
        },
      });
    },
    goModesUsage() {
      this.$router.push({
        path: "/newLights/modesUsage",
        query: {
          from: this.from,
          language: this.$i18n.locale,
          deviceId: this.deviceId,
          fromPageName: "modesUsage",
        },
      });
    },
