      this.FunctionalOntimeText = value;
      if(index == 0){
        this.FunctionalOntime = index + 6;
      }else{
        this.FunctionalOntime = index - 1;
      }
      console.log("FunctionalOntime的值",this.FunctionalOntime);
      this.showPop = false;
      this.onforPop = false;

