    handleShelfLightsBrightnessDrag(){
      this.$fireBase.gaWebReport(this.from,'move','functional_mode_shelf_lights_brightness',{"FunctionalLightsIntensity":this.FunctionalLightsIntensity})
      this.dragFlag = false
    },
    //light tempreature
    handleLightsTemperature(val) {
      if(this.onlineStatus == "2" || this.RemoteControlModeFlag == 0 || (this.sabbathmode == 1 && this.sabbathModeStatus == 2)){
        if(this.onlineStatus == "2"){
          this.$toast(this.$t("lang.appliance_offline"));
        }else if (this.RemoteControlModeFlag == 0) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
        } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
        }
        this.FunctionalLightsColour = Number(this.init_data.FunctionalLightsColour)
        this.getLightsInfo();
      } else {
        if(!this.dragFlag) this.$fireBase.gaWebReport(this.from,'click','functional_mode_shelf_lights_light_temperature',{"FunctionalLightsColour":val})
        this.FunctionalLightsColour = val;
      }
    },
    handleLightsTemperatureDrag(){
      this.$fireBase.gaWebReport(this.from,'move','functional_mode_shelf_lights_light_temperature',{"FunctionalLightsColour":this.FunctionalLightsColour})
      this.dragFlag = false
    },
    // RBG切换开关
    RGBlightsSwitch(val) {
      // if (!val) {
      //   this.FunctionalRGBlightIntensity = 0;
      // } else {
      // }
    },
    //RGB slider
    handleRGBLightBrightness(val) {
      if(this.onlineStatus == "2" || this.RemoteControlModeFlag == 0 || (this.sabbathmode == 1 && this.sabbathModeStatus == 2)){
        if(this.onlineStatus == "2"){
          this.$toast(this.$t("lang.appliance_offline"));
        }else if (this.RemoteControlModeFlag == 0) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
        } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
        }
        this.FunctionalRGBlightIntensity = Number(this.init_data.FunctionalRGBlightIntensity)
        this.getLightsInfo();
      } else {
        if(!this.dragFlag)this.$fireBase.gaWebReport(this.from,'click','functional_mode_rgb_lights_light_temperature',{"FunctionalRGBlightIntensity":val})
        this.FunctionalRGBlightIntensity = val
        // if (val > 0) {
        //   this.RGBLightOnoff = true;
        // } else {
        //   this.RGBLightOnoff = false;
        // }
      }
    },
    handleRGBLightBrightnessDrag(){
      this.$fireBase.gaWebReport(this.from,'move','functional_mode_shelf_lights_light_temperature',{"FunctionalRGBlightIntensity":this.FunctionalRGBlightIntensity})
      this.dragFlag = false
    },
    //跳转颜色选择界面
    selectColor() {
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      }else if (this.RemoteControlModeFlag == 0) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      } else {
        let params = {
          language: this.$i18n.locale,
          from: this.from,
          deviceId: this.deviceId,
          fromPageName: "functionalMode",
        };
        this.$commonMethods.routerGo(this.$router, "/newLights/colorSelect", params);
      }
    },
    //获取灯光信息
    getLightsInfo() {
      // this.showLoading = true;
      let type = { deviceId: this.deviceId };

      //刷新数据
      // if(this.from=='ios'){
      //   window.webkit.messageHandlers.getDevicePropertiesInfo.postMessage('')
      // }else if(this.from=='android'){
      //   window.wine.getDevicePropertiesInfo(this.nativeKey, JSON.stringify(type))
      // }

      this.$native.getLightsInfo(
        this.from,
        this.nativeKey,
        JSON.stringify(type)
      );
      if (this.from == "html") {
        let lightsInfo = {
          errorDesc: "获灯光信息成功",
          resultCode: "0",
          data: {
            FunctionalLightsIntensity: "20", // brightness
            FunctionalLightsColour: "20",
            FunctionalRGBlightIntensity: "10",
            FunctionalRcolour: "125",
            FunctionalGcolour: "125",
            FunctionalBcolour: "125",
            FunctionalOntime: "6",
            RemoteControlMode: "1",
          },
        };
        this.getLightsCallback(lightsInfo);
      }
    },
    //获取灯光信息回调
    getLightsCallback(val) {
      console.log("Functional页面 getLightsCallback返回的数据", val);
      // console.log(val)
      if (val.resultCode == "0" || val.resultCode == 0) {
        if ("data" in val) {
          let lightsInfo = val.data;
          if(JSON.stringify(this.init_data)=="{}"){
            //v12用
            this.FunctionalOntime = Number(lightsInfo.FunctionalOntime) || 0;
            if(this.FunctionalOntime == 6){
              this.FunctionalOntimeText = this.presenseSensorDisplayList[0];
            }else{
              this.FunctionalOntimeText = this.presenseSensorDisplayList[this.FunctionalOntime + 1];
            }
            this.FunctionalLightsIntensity = Number(lightsInfo.FunctionalLightsIntensity) || 0;
            if (this.FunctionalLightsIntensity > 0) {
              this.lightsFunctionalOnoff = true;
            } else {
              this.lightsFunctionalOnoff = false;
            }

            if(this.oneZoneFlag == 0){//三温区
              this.FunctionalLightsColour = 23 - Number(lightsInfo.FunctionalLightsColour) || 0;
            }else{
              this.FunctionalLightsColour = Number(lightsInfo.FunctionalLightsColour) || 0;
            }
            this.FunctionalRGBlightIntensity = Number(lightsInfo.FunctionalRGBlightIntensity) || 0;

            if (this.FunctionalRGBlightIntensity > 0) {
              this.RGBLightOnoff = true;
            } else {
              this.RGBLightOnoff = false;
            }
            this.FunctionalRColour = Number(lightsInfo.FunctionalRcolour) || 0;
            this.FunctionalGColour = Number(lightsInfo.FunctionalGcolour) || 0;
            this.FunctionalBColour = Number(lightsInfo.FunctionalBcolour) || 0;
            const FunctionalColour ="rgb(" + this.FunctionalRColour + "," + this.FunctionalGColour + "," + this.FunctionalBColour + ")";
            console.log('功能灯RGB灯颜色',FunctionalColour)
            console.log('FunctionalRColour{}',this.FunctionalRColour)
            console.log('FunctionalGColour{}',this.FunctionalGColour)
            console.log('FunctionalBColour{}',this.FunctionalBColour)
            this.$store.commit("lights/FUNCTIONAL_R_COLOUR",this.FunctionalRColour);
            this.$store.commit("lights/FUNCTIONAL_G_COLOUR",this.FunctionalGColour);
            this.$store.commit("lights/FUNCTIONAL_B_COLOUR",this.FunctionalBColour);
            this.$store.commit("lights/FUNCTIONAL_COLOUR", FunctionalColour);
            this.RemoteControlModeFlag = Number(lightsInfo.RemoteControlMode);
            console.log("lightsInfo.RemoteControlMode",lightsInfo.RemoteControlMode);

            console.log('_FunctionalColour{}里的功能灯RGB数据',this._FunctionalColour)
            this.init_data = lightsInfo;
          }

          if(this.init_data.FunctionalOntime != Number(lightsInfo.FunctionalOntime)){
            //v12用
            this.init_data.FunctionalOntime = Number(lightsInfo.FunctionalOntime)
            this.FunctionalOntime = Number(lightsInfo.FunctionalOntime) || 0;
            if(this.FunctionalOntime == 6){
              this.FunctionalOntimeText = this.presenseSensorDisplayList[0];
            }else{
              this.FunctionalOntimeText = this.presenseSensorDisplayList[this.FunctionalOntime + 1];
            }
          }
          if(this.init_data.FunctionalLightsIntensity != Number(lightsInfo.FunctionalLightsIntensity)){
            this.init_data.FunctionalLightsIntensity = Number(lightsInfo.FunctionalLightsIntensity)
            this.FunctionalLightsIntensity = Number(lightsInfo.FunctionalLightsIntensity) || 0;
            if (this.FunctionalLightsIntensity > 0) {
              this.lightsFunctionalOnoff = true;
            } else {
              this.lightsFunctionalOnoff = false;
            }
          }
          if(this.init_data.FunctionalLightsColour != Number(lightsInfo.FunctionalLightsColour)){
            this.init_data.FunctionalLightsColour = Number(lightsInfo.FunctionalLightsColour)
            if(this.oneZoneFlag == 0){//三温区
              this.FunctionalLightsColour = 23 - Number(lightsInfo.FunctionalLightsColour) || 0;
            }else{
              this.FunctionalLightsColour = Number(lightsInfo.FunctionalLightsColour) || 0;
            }
          }
          if(this.init_data.FunctionalRGBlightIntensity != Number(lightsInfo.FunctionalRGBlightIntensity)){
            this.init_data.FunctionalRGBlightIntensity = Number(lightsInfo.FunctionalRGBlightIntensity)
            this.FunctionalRGBlightIntensity = Number(lightsInfo.FunctionalRGBlightIntensity) || 0;
            if (this.FunctionalRGBlightIntensity > 0) {
              this.RGBLightOnoff = true;
            } else {
              this.RGBLightOnoff = false;
            }
          }
          if(this.init_data.FunctionalRcolour != Number(lightsInfo.FunctionalRcolour)){
            this.init_data.FunctionalRcolour = Number(lightsInfo.FunctionalRcolour)
            this.FunctionalRColour = Number(lightsInfo.FunctionalRcolour) || 0;
            this.$store.commit("lights/FUNCTIONAL_R_COLOUR",this.FunctionalRColour);
          }
          if(this.init_data.FunctionalGcolour != Number(lightsInfo.FunctionalGcolour)){
            this.init_data.FunctionalGcolour = Number(lightsInfo.FunctionalGcolour)
            this.FunctionalGColour = Number(lightsInfo.FunctionalGcolour) || 0;
            this.$store.commit("lights/FUNCTIONAL_G_COLOUR",this.FunctionalGColour);
          }
          if(this.init_data.FunctionalBcolour != Number(lightsInfo.FunctionalBcolour)){
            this.init_data.FunctionalBcolour = Number(lightsInfo.FunctionalBcolour)
            this.FunctionalBColour = Number(lightsInfo.FunctionalBcolour) || 0;
            this.$store.commit("lights/FUNCTIONAL_B_COLOUR",this.FunctionalBColour);
          }
          if(this.init_data.RemoteControlModeFlag != Number(lightsInfo.RemoteControlMode)){
            this.init_data.RemoteControlModeFlag = Number(lightsInfo.RemoteControlMode)
            this.RemoteControlModeFlag = Number(lightsInfo.RemoteControlMode);
          }
          console.log('FunctionalRColour',this.FunctionalRColour)
          console.log('FunctionalGColour',this.FunctionalGColour)
          console.log('FunctionalBColour',this.FunctionalBColour)
          const FunctionalColour ="rgb(" + this.FunctionalRColour + "," + this.FunctionalGColour + "," + this.FunctionalBColour + ")";
          this.$store.commit("lights/FUNCTIONAL_COLOUR", FunctionalColour);
          console.log('_FunctionalColour里的功能灯RGB数据',this._FunctionalColour)
        }
      } else {
        this.$toast(this.$t("lang.failure"));
      }

      this.showLoading = false;
    },
    saveHandle() {
      console.log(
        "RemoteControlModeFlag==========",
        this.RemoteControlModeFlag
      );
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      }else if (this.RemoteControlModeFlag == 0) {
        console.log("RemoteControlModeFlag等于0时走这里");
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
        // 新增功能灯安息日deactivated模式 判断
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      } else {
        // this.showLoading = true;
        console.log("点击保存");

        let params = {}
        if(this.oneZoneFlag == 0){
          params =  {
            FunctionalLightsIntensity: this.FunctionalLightsIntensity.toString(),
            FunctionalLightsColour: (
              23 - Number(this.FunctionalLightsColour)
            ).toString(),
            FunctionalRGBlightIntensity: this.FunctionalRGBlightIntensity.toString(),
            FunctionalOntime: this.FunctionalOntime.toString(),
          };
        } else {
          params =  {
            FunctionalLightsIntensity: this.FunctionalLightsIntensity.toString(),
            FunctionalLightsColour: (
              // 23 - Number(this.FunctionalLightsColour)
              Number(this.FunctionalLightsColour)
            ).toString(),
            FunctionalOntime: this.FunctionalOntime.toString(),
          };
        }
        this.$fireBase.gaWebReport(this.from,'save_btn','functional_mode_save',params)
        console.log("点击保存的入参params", JSON.stringify(params));
        // alert("sendCmd")
        this.$native.sendCmd(
          this.from,
          this.nativeKey,
          this.deviceId,
          JSON.stringify(params)
        );
        if (this.from == "html") {
          let res = {
            resultCode: "0",
          };
          this.sendCmdCallback(res);
        }
      }
    },
    sendCmdCallback(data) {
      this.showLoading = false;
      console.log("sendCmdCallback返回数据_functional页面", data);
      if (data.resultCode == "0" || data.resultCode == 0) {
        // alert("functional1")
        this.$toast(this.$t("lang.success"));
        let params = {
          language: this.$i18n.locale,
          from: this.from,
          deviceId: this.deviceId,
        };
        this.$commonMethods.routerGo(this.$router, "/newLights/newLights", params);
      } else {
        this.showLoading = false;
        this.$toast(this.$t("lang.failure"));
      }
    },
    //物理返回键
    onBackPressedCallback() {
      this.goBack();
    },
  },
  components: {
    commonHeader: () => import("@/components/common/commonHeader"),
    commonBottom: () => import("@/components/common/commonBottom"),
    loading: () => import("@/components/common/loading"),
  },
};
</script>
<style lang="less" scoped>
/* 头部 */
#wine_lights .common_header_left {
  position: absolute;
  top: 0;
  left: 0;
  width: 19%;
  height: 44px;
  overflow: hidden;
  cursor: pointer;
  i {
    display: block;
    margin: 14px auto 14px 20px;
    font-size: 16px;
    // font-family: Arial;
  }
}
#wine_lights .common_header_center {
  position: absolute;
  top: 0;
  left: 19%;
  width: 80%;
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  color: #000;
  // font-family: Arial;
}
/* 主体 */
/* 主体 */
#wine_lights .lights_tab_card_content {
  position: absolute;
  top: 64px;
  left: 0;
  right: 0;
  bottom: 0px;
  padding-top: 40px;
  color: #000;
  font-size: 16px;
  // font-family: Arial;
  background: #e9e9e9;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  .lights_content {
    width: 100%;
    overflow: hidden;
    .lights_switch {
      width: 83%;
      height: 39px;
      margin: 0 auto 10px;
      .lights_switch_title {
        float: left;
        width: 80%;
        height: 39px;
        line-height: 39px;
        font-size: 20px;
        // font-family: Arial;
        color: #000;
        font-weight: bold;
      }
      .lights_switch_btn {
        float: right;
        width: 17%;
        height: 39px;
        padding-top: 4px;
      }
    }
    .lights_item {
      width: 83%;
      margin: 10px auto 0;
      .lights_title {
        width: 100%;
        margin-bottom: 19px;
        font-size: 18px;
        color: #000;
        // font-family: Arial;
        font-weight: 800;
      }
      .lights_bright_percent {
        text-align: right;
        width: 100%;
        margin-bottom: 10px;
        font-size: 18px;
        height: 20px;
        line-height: 20px;
        font-weight: 800;
        // font-family: Arial;
      }
      .lights_bright_percent_show {
        display: flex;
        margin-top: 5px;
        justify-content: space-between;
        align-items: center;
        span {
          font-size: 10px;
          font-weight: 700;
          // font-family: Arial;
        }
      }
      .lights_disable_block {
        width: 100%;
        height: 24px;
        margin: 0 auto;
        background: #c4c4c4;
        opacity: 0.3;
        filter: alpha(opacity=30);
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        -ms-border-radius: 12px;
        -o-border-radius: 12px;
        border-radius: 12px;
      }
      .lights_time {
        width: 100%;
        font-size: 16px;
        // font-family: Arial;
      }
      .ambient_color {
        width: 100%;
        height: 24px;
        margin: 0 auto;
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        -ms-border-radius: 12px;
        -o-border-radius: 12px;
        border-radius: 12px;
        cursor: pointer;
      }
    }
    .lights_item_ambient {
      margin: 24px auto 0;
    }
  }
}
#wine_lights .lights_slider_wrap {
  width: 100%;
  height: 24px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
  border-radius: 12px;
}
// #wine_lights .van-switch {
//   width: 51px;
//   height: 30px;
//   -webkit-border-radius: 15px;
//   -moz-border-radius: 15px;
//   -ms-border-radius: 15px;
//   -o-border-radius: 15px;
//   border-radius: 15px;
//   border: 1px solid #000;
// }
// #wine_lights .van-switch__node {
//   top: 0;
//   left: -1px;
//   width: 30px;
//   height: 30px;
//   border: 1px solid #000;
// }
// #wine_lights .van-switch--on .van-switch__node {
//   -webkit-transform: translateX(23px);
//   transform: translateX(23px);
// }
#wine_lights .van-slider {
  width: 87%;
  height: 24px;
  margin: 0 auto;
  background: none;
}
#wine_lights .van-slider .lights_button {
  position: relative;
  width: 40px;
  height: 24px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
  border-radius: 12px;
}
.onTime {
  padding-bottom: 120px !important;
}
</style>



